:{"font-size":"14px"}},[_vm._v("2. 이용 약관 변경")]),_c('br'),_vm._v(" 2-1. 잼플랫폼은 필요할 경우 약관을 수정하거나 변경할 수 있습니다. 약관이 변경되는 경우 잼플랫폼은 약관의 변경 내용 및 사유를 공지하고, 필요할 경우 이메일 발송 등 적절한 방법으로 통지합니다. 만약 사용자가 연락처를 기재하지 않거나 변경 등으로 인하여 개별 통지가 어려운 경우에는 잼플랫폼 사이트에 변경 약관을 공지함으로써 개별 통지한 것으로 봅니다. "),_c('br'),_vm._v(" 2-2. 사용자가 변경된 약관의 효력 발생일까지 약관 변경에 대한 거부의사를 명시적으로 표시하지 않거나 약관 변경 이후에 잼서비스를 이용할 경우 변경된 약관에 동의한 것으로 봅니다. 사용자가 변경된 약관에 동의하지 않는 경우, 잼플랫폼 사이트가 제공하는 잼서비스 이용이 제한되거나 정지될 수 있습니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("3. 이용 약관 해지 및 해지의 결과")]),_c('br'),_vm._v(" 3-1. 약관은 사용자나 잼플랫폼이 잼서비스 이용을 해지하지 전까지 계속하여 효력을 갖습니다. "),_c('br'),_vm._v(" 3-2. 잼플랫폼은 등록 데이터의 일부로 잼플랫폼에 제공된 이메일 주소를 사용하거나 해지 통지를 받을 수 있는 기타 적절한 방식을 통해 해지에 관한 서면 통지를 제공하여 언제든지 잼서비스 이용을 중단하거나 폐지할 수 있습니다. "),_c('br'),_vm._v(" 3-3. 다음과 같은 경우 잼플랫폼은 언제든지 즉시 본 계약을 해지할 수 있습니다. "),_c('br'),_vm._v(" 3-3-a. 사용자가 약관을 중대하게 위반한 경우 "),_c('br'),_vm._v(" 3-3-b. 사용자가 약관을 준수할 의도가 없음을 직접, 행동, 진술 또는 기타 방식으로 명백히 표시한 경우 "),_c('br'),_vm._v(" 3-3-c. 잼플랫폼이 잼서비스를 사용자에게 제공하거나, 잼플랫폼과 함께 잼서비스를 제공하는 잼플랫폼의 공급자 또는 파트너가 잼서비스 전체 또는 그 일부의 제공을 종료하기로 결정하거나, 잼플랫폼의 공급자 또는 파트너가 잼플랫폼과의 모든 관계를 해지하기로 결정하는 경우 "),_c('br'),_vm._v(" 3-3- d. 잼플랫폼이 잼서비스를 사용자에게 제공하거나, 잼플랫폼과 함께 잼서비스를 제공하는 잼플랫폼의 공급자나 파트너가 관련 법률에 따라 잼서비스나 그 일부의 제공을 종료해야 하는 경우 (예를 들어 관련 법률의 변경 또는 법원의 판결이나 판정에 따라 잼서비스 또는 그 일부가 불법이 되거나 불법으로 간주되는 경우) "),_c('br'),_vm._v(" 3-3-4. 약관의 해지는 약관의 유효 기간 중 발생하거나 초래된 사용자 또는 잼플랫폼의 권리, 의무 및 책임에 영향을 미치지 않습니다. "),_c('br'),_vm._v(" 3-3-5. 사용자는 수정된 약관에 동의하지 않거나, 사용자나 잼플랫폼에 의한 해지 등으로 잼서비스가 종료된 후에는 잼서비스를 이용할 수 없습니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("4. 잼서비스 제공 및 이용 제한")]),_c('br'),_vm._v(" 4-1. 잼플랫폼은 사용자에게 잼서비스를 제공함에 있어 잼서비스 사용자가 글로벌 이커머스 비즈니스를 영위하여 상품매출을 획득하기 위한 목적이 아닌, 비즈니스 모델의 취득, 동일한 서비스 제공하는 사업을 위한 목적으로 사용할 시 즉시 이용을 제한하고, 그 손해를 대한민국 법률에 의거 청구할 수 있습니다. "),_c('br'),_vm._v(" 4-2. 본 이용 약관에 달리 명시되지 않는 한 사용자는 개인적이며 비상업적인 용도와 목적에 한정하여 잼서비스를 이용할 수 있으며 모든 잼서비스 또는 그 일부를 가공, 복제, 복사, 판매, 거래, 재판매 또는 영리목적으로 이용할 수 없습니다. "),_c('br'),_vm._v(" 4-3. 사용자는 잼서비스를 이용하면서 항상 약관 및 거주지 또는 잼서비스 이용 관할지를 포함한 관련 관할지의 관련 법률이나 규정을 준수해야 합니다. "),_c('br'),_vm._v(" 4-4. 잼플랫폼은 언제든지 자신의 독자적인 판단에 따라 사전 경고 또는 통지 없이 다음과 같은 조치를 할 수 있습니다. "),_c('br'),_vm._v(" 4-4-a. 잼서비스 변경 또는 잼서비스나 그 일부의 제공을 일시 중단 및/또는 종료 "),_c('br'),_vm._v(" 4-4-b. 잼서비스를 통해 사용자가 보내거나 받는 전송 횟수 또는 잼서비스나 그 일부를 사용자에게 제공하는데 사용되는 저장 공간의 크기 제한 "),_c('br'),_vm._v(" 4-4-c. 잼서비스를 통해 제공되는 일부 또는 모든 오디오 및 사운드 파일, 데이터 파일, 이미지, 음악, 사진, 소프트웨어, 동영상 및 작성된 텍스트 (이하 “콘텐츠”) 를 사전 심사, 검토, 플래그 표시, 필터링, 수정, 거부, 거절, 액세스 차단 또는 제거 "),_c('br'),_vm._v(" 4-5. 잼플랫폼은 부당한 중단, 장애 또는 지연없이 사용자에게 잼서비스가 제공되도록 하며 잼서비스의 중단, 장애 또는 지연을 최소화하기 위해 합리적인 노력을 기울입니다. "),_c('br'),_vm._v(" 4-6. 사용자는 잼플랫폼이 인터페이스를 통해 제공하는 것 이외에 다른 잼서비스를 이용할 수 없으며 (이용에는 이용하려는 시도를 포함합니다. 이아 동일) 자동화된 도구 (소프트웨어 및/또는 하드웨어 포함), 기능, 잼서비스 또는 기타 방식 (스크립트 또는 웹 크롤러 포함)을 통해 잼서비스를 이용하지 않습니다. "),_c('br'),_vm._v(" 4-7. 사용자는 잼서비스와 관련하여 잼플랫폼이 사용자에게 제공하는 모든 지침을 준수하고 따르며 잼서비스 또는 잼서비스에 연결된 모든 서버, 네트워크 또는 기타 장비에 장애나 중단을 초래할 수 있는 행위에 관여하지 않습니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("5. 잼서비스에 제공되는 콘텐츠 및 콘텐츠 권한")]),_c('br'),_vm._v(" 1. 잼서비스에서 제공되거나 잼서비스 이용의 일부로 또는 잼서비스 이용을 통해 액세스할 수 있는 콘텐츠는 창작자에게 보든 책임과 권리가 있습니다. 콘텐츠에는 광고, 프로모션 자료 및 문서나 기타 후원 대상 콘텐츠가 포함될 수 있습니다. "),_c('br'),_vm._v(" 2. 콘텐츠는 제3자의 소유권 또는 지식 재산권에 따라 보호될 수 있습니다. 사용자는 콘텐츠의 전부 또는 일부를 수정, 대여, 임대, 임차, 판매, 배포 또는 이에 기반을 둔 파생물을 생성하거나 콘텐츠에 대한 라이센스를 허가할 수 없습니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("6. 잼플랫폼 잼서비스 라이센스")]),_c('br'),_vm._v(" 1. 잼플랫폼은 사용자에게 잼서비스 이용 (잼서비스의 일부로 사용자에게 제공된 모든 소프트웨어, 제품 또는 자료 포함)에 대한 전 세계적인 이전 불가, 양도 불가, 비독점적, 개인적 및 무상의 라이센스를 잼플랫폼이 사용자에게 제공한 형태로 운영됩니다. 잼플랫폼 라이선스를 통해 사용자는 본 이용 약관에 따라 잼서비스의 혜택을 이용할 수 있습니다. "),_c('br'),_vm._v(" 2. 본 이용 약관에서 사용자에게 부여된 권리를 제외하고 잼서비스에 과한 모든 권리는 잼플랫폼이 보유합니다. "),_c('br'),_vm._v(" 3. 사용자는 잼플랫폼의 사전 서명 동의 없이 잼플랫폼 잼서비스 (또는 그 일부)의 소스 코드를 복사, 편집, 수정, 변경 또는 파생물 생성, 또는 다른 방식으로 추출할 수 없고, 이러한 권리를 제3자에게 허가할 수 없습니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("7. 등록 데이터")]),_c('br'),_vm._v(" 1. 잼서비스의 전부 또는 일부를 이용할 경우 사용자는 본인에 대한 정보 (“등록 데이터”)를 제공해야만 잼서비스를 이용할 수 있습니다. "),_c('br'),_vm._v(" 2. 사용자는 정확한 현재의 등록 데이트를 제공하고 이를 지속적으로 정확한 상태로 유지하기 위해 수시로 등록 데이터를 갱신하여야 합니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("8. 개인 정보 및 개인 데이터 보호")]),_c('br'),_vm._v(" 잼플랫폼은 사용자의 개인 데이터 보호를 위해 최선을 다합니다. 사용자의 잼서비스 이용 시 잼플랫폼이 사용자의 개인 데이터 및 정보를 보호하고 취급하는 방법은 회사의 개인정보처리방침에서 자세히 확인할 수 있습니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("9. 일반 조항")]),_c('br'),_vm._v(" 1. 잼플랫폼과 잼서비스 사용자 사이의 법률관계에는 약관만 적용됩니다. 직접 또는 간접으로든, 서면 또는 구두로든, 또는 광고를 통해서든 다른 곳에서 이루어 지거나 체결된 어떠한 진술, 선언 또는 합의도 잼플랫폼이 사용자에게 서면을 통해 명시적으로 확인하지 않은 한 잼플랫폼에 구속력을 갖지 않습니다. "),_c('br'),_vm._v(" 2. 약관의 어떤 조항이 관할 법원에서 무효, 불법 또는 집행 불가능 (전체 또는 일부)으로 판정되더라도 나머지 조항에 대한 유효성, 합법성 및 집행 가능성은 영향을 받지 않습니다. "),_c('br'),_vm._v(" 3. 잼서비스 사용자는 잼플랫폼의 사전 서면 동의 없이 사용자와 개별적으로 체결된 약관이나 약관에 따라 발생한 잼서비스 사용자의 권리 또는 책임을 제3자에게 이전, 양도, 담보제공 또는 다른 방식으로 처분할 수 없습니다. "),_c('br'),_vm._v(" 4. 약관에 따른 잼서비스 사용자와 잼플랫폼과의 관계는 국제사법 규정에도 불구하고 대한민국의 법률이 적용됩니다. 잼플랫폼과 귀하 간의 본 잼서비스에 관한 소송은 제소 당시의 귀하의 주소를 관할하는 법원의 전속 관할로 하고, 주소가 없는 경우 거소를 관할하는 법원의 전속 관할로 합니다. 다만, 제소 당시 귀하의 주소 또는 거주지가 분명하지 아니한 경우에는 대한민국 서울중앙지방법원의 전속관할로 정합니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("10. 환불")]),_c('br'),_vm._v(" 1. 회원은 회사에 환불을 요구할 수 있습니다. 환불은 회사가 안내하는 정책 및 방법에 따라 진행됩니다. "),_c('br'),_vm._v(" 1-1. 배송이 불가한 경우 전액 환불처리됩니다. "),_c('br'),_vm._v(" 1-2. 카드결제로 구매한 건의 환불은 카드결제 취소를 통해서만 가능합니다. ")]),_c('v-switch',{staticClass:"d-inline-block",attrs:{"hide-details":""},on:{"click":_vm.setTotalAgreeCheck},model:{value:(_vm.switch1),callback:function ($$v) {_vm.switch1=$$v},expression:"switch1"}})],1),_c('v-col',{staticClass:"ma-1 pa-0",attrs:{"align":"right"}},[_c('div',{staticStyle:{"margin-top":"10px","height":"400px","border":"solid 1px #B7B7B7","overflow":"scroll","font-size":"12px"},attrs:{"align":"left"}},[_vm._v(" “잼플랫폼 글로벌 이커머스 로지스틱스 서비스(GEM Platform Global Ecommerce Logistics Service)” 이용 약관 (이하 \"약관\"이라 합니다)은 “주식회사 잼플랫폼”(이하 \"잼플랫폼\"이라 합니다)이 개발 및 운영하는 잼플랫폼 사이트 (www.gemplatforms.com)에서 제공하는 글로벌 이커머스 물류서비스 및 연계된 모든 서비스인 “잼서비스” (이하 “GEM Service”이라 합니다)를 이용함에 잼서비스 사용자는 송하인으로서, 운송물에 관련된 당사자 본인이나 관련 이해 당사자를 대리하여, 적법한 권한을 위임받은 주식회사 잼플랫폼 소속 관리자와 다른 내용의 서면계약을 하지 않은 한, 잼플랫폼이 운송물을 인수받은 시점부터 본 약관 기준에 동의한 것으로 봅니다. 사용자의 법적인 권리나 권한은 정해진 서비스의 형태(일부 추가비용이 들었더라도)에 따라서 영향을 받지 않습니다. "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("1. 통관, 수출입")]),_c('br'),_vm._v(" 잼플랫폼은 송하인을 대리하여 물류서비스를 제공하기 위해 다음의 활동을 수행할 수 있습니다. "),_c('br'),_vm._v(" (1) 서류의 작성, 상품이나 서비스 Code의 정정 또는 관련 법령이나 규정하의 관세나 세금의 지불 등을 하게 되며 "),_c('br'),_vm._v(" (2) 발송자의 수출 통관, 수출관리 목적의 대행자로서 또는 수취인의 수입통관 및 등록의 업무를 수행할 관세사의 지정을 위한 수하인으로의 역할 "),_c('br'),_vm._v(" (3) 운송사는 아래와 같은 사유 발생시 정당한 권한이 있는 것으로 판단한 제3자의 요청에 따라 현물을 인계할 수 있습니다. "),_c('br'),_vm._v(" - 최종 배송지 수취인이 부재 중일 경우 "),_c('br'),_vm._v(" - 최종 배송지의 보안 상의 문제로 수취인 직접 인계 불가할 경우(예. 문서 수발실, 경비실 등) "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("2. 취급불가 운송물")]),_c('br'),_vm._v(" 아래와 같은 물품은 수령하지(취급하지) 않습니다. "),_c('br'),_vm._v("  IATA(국제항공운송협회) 또는 ICAO(국제민간항공협회), 관련국 정부, 기타 유관기관 등에 의해 독극물, 위험물, 금지 또는 제한됨 품목 "),_c('br'),_vm._v("  세관규칙 상 필요함에도 세관신고를 하지 않은 경우 "),_c('br'),_vm._v("  안전하게 또는 합법적으로 운송할 수 없다고 판단되는 물품(그런 물품에는 동물류, 현금이나 유가증권, 귀금속, 화약류, 군수품, 유골, 포르노그라피, 불법약물이나 마약류 등이 포함되며 이에 한정되지 않습니다.) "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("3. 배달 및 배달불가")]),_c('br'),_vm._v(" 운송물은 송하인이 제공하는 수하인 주소지로 배달되게 되나(단, 우편서비스의 경우 최초의 인수 우체국이 됨) 수취인이 반드시 개인이 될 필요는 없습니다. 중앙수취지역으로 되어있는 운송물은 그 지역으로 배달됩니다. "),_c('br'),_vm._v(" 만약 아래와 같은 사유 발생 시 운송사는 운송물을 수취인께 되돌려 드리기 위한 합리적인 노력은 하되, 이때 관련된 서비스 비용이나 행정처리 비용 등은 송하인에게 부과됩니다. "),_c('br'),_vm._v(" - 수취인이 발송물의 수취거절 "),_c('br'),_vm._v(" - 수취인이 관련 비용(운임/관부가세 등 운송관련 정당한 비용) 지불 거절 "),_c('br'),_vm._v(" - 배송지 국가의 세관규정 상, 운송물이 취급 불가한 것으로 간주되거나, 세관 통관상 Undervalue 등 문제 발생 시 "),_c('br'),_c('br'),_c('b',{staticStyle:{"font-size":"14px"}},[_vm._v("4. 검사")]),_c('br'),_vm._v